@import"https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;700&family=Space+Grotesk:wght@400;600;700&display=swap";.overlay{position:fixed;top:0;left:0;width:100%;height:100%;background:rgba(0,0,0,.85);backdrop-filter:blur(5px);display:flex;justify-content:center;align-items:center;z-index:1000;transition:opacity .3s ease}.overlay.closing{opacity:0}.popup{position:relative;width:90%;max-width:650px;background:#000;padding:45px 35px;border-radius:12px;box-shadow:0 15px 35px rgba(0,0,0,.5);color:#fff;font-family:"Space Grotesk",sans-serif;border:1px solid rgba(255,255,255,.15);transition:transform .3s ease,opacity .3s ease}.popup.closing{transform:scale(0.95);opacity:0}.closeButton{position:absolute;top:15px;right:15px;width:35px;height:35px;border-radius:50%;background:#fff;color:#000;display:flex;align-items:center;justify-content:center;cursor:pointer;font-size:24px;font-weight:bold;border:none}.closeButton:hover{background:#ff3b3b;color:#fff}.title{text-align:center;font-size:3rem;font-weight:700;letter-spacing:-0.03em;text-transform:uppercase;margin-bottom:1.5rem;color:#fff;text-shadow:0 0 10px rgba(255,255,255,.3)}.tagline{text-align:center;font-size:1.6rem;font-weight:600;margin-bottom:1.8rem;color:#fff;letter-spacing:.02em}.mainText{margin-bottom:30px;font-size:1.4rem;line-height:1.5;font-weight:400;color:#fff}.mainText strong,.mainText span{color:#ff3b3b;font-weight:700}.list{margin:30px 0;padding:0;list-style:none}.list li{font-size:1.35rem;margin-bottom:20px;position:relative;padding-left:30px;line-height:1.4;font-weight:400;color:#fff}.list li:before{content:"";position:absolute;left:0;color:#ff3b3b;font-weight:bold;font-size:1.8rem}.one-stop{color:#ff3b3b;font-weight:700;text-shadow:0 0 10px rgba(255,59,59,.3)}.outro{text-align:center;font-size:1.5rem;font-weight:700;text-transform:uppercase;letter-spacing:.15em;margin-top:35px;color:#fff;padding:15px 0}@media(max-width: 768px){.popup{padding:40px 25px}.title{font-size:2.5rem}.tagline{font-size:1.4rem}.mainText{font-size:1.2rem}.list li{font-size:1.2rem}.outro{font-size:1.3rem}}@media(max-width: 480px){.popup{padding:35px 20px}.title{font-size:2.2rem}.tagline{font-size:1.2rem}.mainText,.list li{font-size:1.1rem}.outro{font-size:1.2rem}}